<template>
    <div>
        <van-nav-bar title="修改密码" left-text="返回" left-arrow @click-left="toback" />
        <van-form @submit="password">
          <van-field v-model="pwds.oldPassword" label="旧密码" placeholder="旧密码" />
          <van-field v-model="pwds.password" type="text" label="新密码" placeholder="新密码" />
          <div class="btn-box">
            <van-button round block type="info" native-type="submit">提交</van-button>
          </div>
        </van-form>
    </div>
</template>

<script>
    export default {
        data(){
            return{
                pwds:{
                    oldPassword:'',
                    password:''
                }
            }
        },
        methods:{
            toback(){
                this.$router.go(-1)
            },
            password(){
                let tokenString = localStorage.getItem("login_Tk");

                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    duration: 0
                });
                this.axios({
                    method: "POST",
                    url: "/updatePassword",
                    data: {
                        appkey: this.appkey,
                        tokenString,
                        //新的密码
                        password: this.pwds.password,
                        //旧密码
                        oldPassword: this.pwds.oldPassword
                    }
                }).then(result => {
                    this.$toast.clear();
                    
                    if (result.data.code == "E001") {
                        //清除token
                        localStorage.removeItem("login_Tk");
                        this.$router.push({ name: "Login" });
                    } else {
                        this.$toast(result.data.msg);
                    }
                })
                .catch(err => {
                    this.$toast.clear();
                    
                });
            }
        }
    }
</script>

<style lang="less" scoped>

</style>